import "./css/style.css";

import { Inter } from "next/font/google";
import localFont from "next/font/local";
import dynamic from "next/dynamic";

import Header from "@/components/ui/header";
import Chat from "@/components/Chat";
import FAQIcon from "@/components/FAQIcon";

// Dynamically import the Draggable component (client-only)
const Draggable = dynamic(
  () => import("react-draggable").then((mod) => mod.default),
  { ssr: false }
);

const inter = Inter({
  subsets: ["latin"],
  variable: "--font-inter",
  display: "swap",
});

const nacelle = localFont({
  src: [
    {
      path: "../public/fonts/nacelle-regular.woff2",
      weight: "400",
      style: "normal",
    },
    {
      path: "../public/fonts/nacelle-italic.woff2",
      weight: "400",
      style: "italic",
    },
    {
      path: "../public/fonts/nacelle-semibold.woff2",
      weight: "600",
      style: "normal",
    },
    {
      path: "../public/fonts/nacelle-semibolditalic.woff2",
      weight: "600",
      style: "italic",
    },
  ],
  variable: "--font-nacelle",
  display: "swap",
});

export const metadata = {
  title: "Monaarch",
  description: "Created By NisaEngineers",
};

export default function RootLayout({
  children,
}: {
  children: React.ReactNode;
}) {
  return (
    <html lang="en">
      <body
        className={`${inter.variable} ${nacelle.variable} bg-gray-950 font-inter text-base text-gray-200 antialiased`}
      >
        <div className="flex min-h-screen flex-col overflow-hidden supports-[overflow:clip]:overflow-clip">
          <Header />
          {/* Wrap Chat and FAQIcon with the draggable component */}
          <Draggable>
            <div>
              <Chat />
            </div>
          </Draggable>
          <Draggable>
            <div>
              <FAQIcon />
            </div>
          </Draggable>
          {children}
        </div>
      </body>
    </html>
  );
}
